*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --beige: #F5F3EC;
    --white: #FFFFFF;
    --gray: #9ca3af;
    --dark-blue: #1C2C59;
    --gold: #F2CA50;
}

html{
    font-size: 62.5%;
    line-height: 1.6;
}

body{
    position: relative;
    font-family: 'Bicyclette','Poppins', sans-serif, system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    background-color: var(--beige) ;
    color: var(--dark-blue);
}

.content{
    display: grid;
    grid-template-columns: 1fr;
}

.nav-bar{
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    padding: 15px 20px;
    background-color: var(--beige);
}

.logo{
    height: 90px;
    width: 150px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.nav-items{
    display: flex;
    font-size: 1.6rem;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}

.nav-buttons{
    font-family: 'Bicyclette';
    color: var(--dark-blue);
    font-weight: 700;
    font-size: 1.8rem;
    background-color: transparent;
    border: none;
}

.active{
    padding-bottom: 3px;
    border-bottom: 3px solid var(--gold);
}

.welcome-section{
    height: calc(100vh - 170px);
    display: grid;
    grid-template: max-content;
    justify-items: center;
    align-content: center;
}

.restaurant-name{
