 700;\n    font-size: 2rem;\n}\n\n.menu-banner, .about-banner{\n    display: grid;\n    place-items: center;\n    height: calc(100vh - 170px);\n    margin: 20px;\n    background-position: center;\n    background-repeat: no-repeat;\n    background-size: cover;\n}\n\n.menu-banner > p, .about-banner > p{\n    font-family: 'Bicyclette';\n    font-size: 5.5rem;\n    font-weight: 700;\n    color: var(--white);\n    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);\n}\n\n.menu-banner{\n    background-image: url(./images/menu-banner.jpg); \n}\n\n.about-banner{\n    background-image: url(./images/about-banner.jpg);\n}\n\n.menu-section, .about-section{\n    display: grid;\n    grid-template: max-content;\n    place-items: center;\n    gap:20px;\n    margin:20px;\n}\n\n.beverages-title, .pastries-title{\n    font-family: 'Bicyclette';\n    font-size: 5rem;\n    font-weight: 700;\n    justify-self: start;\n    padding: 20px 70px;\n    margin-left: 100px;\n}\n\n.pastries-title{\n    justify-self: end;\n    margin-right: 100px;\n}\n\n.beverages-menu, .pastries-menu{\n    display: grid;\n    grid-template-columns: 1fr 1fr ;\n    margin:20px;\n    gap: 60px;\n}\n\n.menu-items{\n    width: 40vw;\n    background-color: var(--dark-blue);\n    color: var(--beige);\n    padding: 100px;\n    padding-bottom: 80px;\n    border-radius: 65px;\n    outline: 5px solid var(--gold);\n    outline-offset: -15px;\n}\n\n.menu-item{\n    font-family: 'Poppins';\n    font-weight: 400;\n    display: flex;\n    justify-content: space-between;\n    font-size: 1.8rem;\n    margin-bottom: 20px;\n}\n\n.beverages-image, .pastries-image, .about-experience-image, .about-menu-image{\n    background-position: center;\n    background-repeat: no-repeat;\n    background-size: cover;\n}\n\n.beverages-image{\n    background-image: url(./images/beverage.jpeg);\n    border-radius: 65px;\n}\n\n.pastries-image{\n    background-image: url(./images/pastry.jpg);\n    background-position: bottom;\n    border-radius: 65px;\n}\n\n.about-experience-image{\n    background-image: url(./images/experience.jpg);\n}\n\n.about-menu-image{\n    background-image: url(./images/menus.jpg);\n}\n\n.about-note{\n    font-family: 'Poppins';\n    font-weight: 300;\n    display: flex;\n    flex-direction: column;\n    text-align: center;\n    width: 800px;\n    font-size: 2rem;\n    gap: 20px;\n    padding:50px;\n}\n\n.tagline{\n    font-family: 'Playfair Display';\n    font-weight: 700;\n    padding: 50px;\n    font-size: 4rem;\n}\n\n.about-menu, .about-experience{\n    display: grid;\n    grid-template-columns: 1fr 1fr ;\n    height: 65vh;   \n    gap: 10px;\n}\n\n.about-menu-note, .about-experience-note{\n    display: grid;\n    place-items: center;\n    border: 5px solid var(--gold);\n}\n\n.context{\n    max-width: 70%;\n    min-width: 70%;\n    margin: auto;\n    text-align: left;\n}\n\n.title{\n    font-weight: 700;\n    font-size: 3rem;\n    padding-bottom: 15px;\n}\n\n.description > *{\n    font-family: 'Poppins';\n    font-weight: 300;\n    padding-bottom : 15px;\n}\n\n.contact-section{\n    height: calc(100vh - 170px);\n    display: grid;\n    justify-items: center;\n    align-content: center;\n    gap: 30px;\n}\n\n.contact-header{\n    font-weight: 700;\n    font-size: 3.6rem;\n}\n\n.contact-subheader{\n    font-family: 'Poppins';\n    font-size: 1.3rem;\n}\n\n.contact-form{\n    font-family: 'Poppins';\n    display: grid;\n    width: 600px;\n    padding: 0px 40px 40px;\n    gap: 10px;\n}\n\n.form-element{\n    display: flex;\n    flex-direction: column;\n    justify-items: center;\n}\n\n.form-element > label{\n    font-size: 1.4rem;\n}\n\n.form-element > label > span{\n    font-size: inherit;\n    color: var(--gray);\n}\n.form-element > input{\n    color: var(--dark-blue);\n    font-size: 1.6rem;\n    padding: 10px;\n    border-radius: 10px;\n    border: none;\n}\n\n.form-element > textarea{\n    font-family: 'Poppins';\n    font-size: 1.6rem;\n    min-height: 150px;\n    max-height: 300px ;\n    resize: vertical;\n    border-radius: 10px;\n    border: none;\n    padding: 10px;\n}\n\n.sendBtn{\n    font-family: 'Bicyclette';\n    font-weight: 700;\n    margin-top: 20px;\n    justify-self: center;\n    padding: 10px 20px;\n    font-size: 1.8rem;\n    background-color:var(--white);\n    color: var(--dark-blue);\n    border: 2px solid var(--dark-blue);\n}\n\n.footer{\n    font-family: 'Poppins';\n    font-weight: 500;\n    font-size: 1.8rem;\n    color: var(--beige);\n    background-color: var(--dark-blue);\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    padding: 20px;\n    margin-top: 40px;\n}\n\nion-icon{\n    font-size: 2.5rem;\n    color: var(--beige);\n    padding-left: 20px;\n}\n\n\nbody::before{\n    background-color: #1C2C59;\n    content: \"\";\n    position: fixed;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    right: 0;\n    background-repeat: repeat;\n    height: 10px;\n    background-size: 20px 20px;\n    background-image:\n    radial-gradient(circle at 10px -5px, transparent 12px, var(--beige) 13px);\n    z-index: 10;\n}\n\n.restaurant-name::before{\n    content: \"\";\n    height: 100px;\n    width: 100px;\n    background-image: url(./icons/frappe.png);\n    background-repeat: no-repeat;\n    background-position: center ;\n    position: absolute;\n    right: 10%;\n    bottom: 15%;\n    transform: rotate(20deg);\n}\n\n.restaurant-name::after{\n    content: \"\";\n    width: 80%;\n    border: 2px solid var(--gold);\n    border-radius: 5px;\n}\n\n.nav-items > button:hover {\n    cursor: pointer;\n    padding-bottom: 3px;\n    border-bottom: 3px solid var(--gold);\n}\n\n.sendBtn:hover{\n    cursor: pointer;\n    background-color: var(--gold);\n}\n\nion-icon:hover{\n    color: var(--gold);\n}"],"sourceRoot":""}]);
